:root{
    --color-black: #222222;
    --color-green:#476A2E;
}

.Navbar{
    width: 100%;
    display:flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 -4px 5px 10px #F3F3F3;
    padding: 10px 0px;
}

.Navbar .container{
    width:75vw;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.Navbar .container img{
    width: 200px;
}


.Navbar .container ul{
    width:180px;
    display: flex;
    align-items: center;
    justify-content: space-between;

}

.Navbar .container ul .Nav-item{
    text-decoration: none;
    font-weight: 600;
    font-size: 18px;
    letter-spacing: .25rem;
    color: var(--color-black);
    transition: .3s linear;
}
.Navbar .container ul .Nav-item:hover{
    color:var(--color-green)
}

@media (max-width:786px) {
.Navbar .container {
        width: 100vw;
        height:100px;
        display: flex;
        flex-direction:column;
        align-items: center;
        justify-content: space-around;
    }
    
}