:root {
    --back-color: #F1F5F8;
    --font-color: #4A4D22;
    --head-color: #222222;
}

.detailCoktail {
    width: 100%;
    height: 93vh;
    background-color: var(--back-color);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 30px 0;
}
.detailCoktail div:first-child{
    width:400px;
    height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.detailCoktail  div .link {
    width: 200px;
    height: 40px;
    background-color: #476A2E;
    border: none;
    border-radius: 7px;
    transition: 0.3s linear;
    margin: 20px 0;
    padding:7px 10px;
    text-decoration: none;
     color: #FFFF;
    letter-spacing: 0.1rem;
    text-transform: capitalize;
    transition: 0.3s linear;
}

.detailCoktail  article{
    width: 80vw;
    height: 450px;
    display: flex;
    flex-direction:row;
    align-items: center;
    justify-content: space-evenly;

}
.detailCoktail article img{
    width: 40%;
    height: 100%;
    border-radius: 8px;
}
.detailCoktail article  div {
    width: 50%;
    height:100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
    
}
.detailCoktail article p{
    width:80%;
    text-align: left;
    line-height: 1.7;
}
.detailCoktail article p:last-child{
    display:flex;
    align-items: center;
}
.detailCoktail article p:last-child span{
    padding-top: 0;
}
.detailCoktail article p span{
    height:30px;
    border-radius: 8px;
    color:var(--font-color);
    background-color:#D4E6A5;
    text-transform: capitalize;
    font-weight:700;
    letter-spacing:.025rem;
    padding: 5px 10px;
    margin-right: 10px;
}
.detailCoktail article p ul{
    width:100%;
    list-style: none;
} 
.detailCoktail article p ul li{
    margin-right: 8px;
}

@media (max-width:1024px) {

    .detailCoktail {
        height: auto;
        justify-content: space-between;
    }
   .detailCoktail article{
       width: 100%;
       height:100vh;
       flex-direction: column;
       justify-content: space-evenly;
       align-items: center;
   }
   .detailCoktail article img {
       width: 50vw;
      height: 45%;
      
   }
   .detailCoktail article div {
       width: 50%;
       height: 50%;
   }
   .detailCoktail article p {
       width: 100%;
       line-height: 2;
       letter-spacing:0.3
   }
}
@media (max-width:768px) {

    .detailCoktail {
        height: auto;
        justify-content: space-between;
    }
   .detailCoktail article{
       width: 100%;
       height:100vh;
       flex-direction: column;
       justify-content: space-evenly;
       align-items: center;
   }
   .detailCoktail article img {
       width: 80vw;
      height: 45%;
      
   }
   .detailCoktail article div {
       width: 80%;
       height: 50%;
   }
   .detailCoktail article p {
       width: 100%;
   }
}