:root{
    --back-color: #F1F5F8;
    --font-color: #4A4D22;
    --head-color: #222222;
}

.home{
    width: 100%;
    min-height:93vh;
    height: auto;
    background-color: var(--back-color);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding:30px 0;
}

.home  form{
    width: 35rem;
    max-width: 450px;
    height:130px;
    background-color:#FFF;
    border-radius: 8px;
    display:flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding:25px;
    font-size: 18px;
    letter-spacing: .20rem;
    color:var(--font-color);
    font-weight:500;
    margin-bottom: 30px;
}
.home form input{
    width: 100%;
    height:40px;
    margin-top: 15px;
    border:none;
    background-color:var(--back-color);
    font-size: 16px;
    padding: 0 10px;
}
.home section{
    display:grid;
    grid-template-columns: repeat(3 , 400px);
    gap: 30px;
    margin-top: 20px;
     align-content: center;
}
.home section article{
    width: 100%;
    height:500px;
    border-radius: 8px;
    background-color: #FFF;
    text-align: left;
 
    
}
.home section article img{
    width:100%;
    height:70%;
    border-radius: 8px 8px  0 0;
}
.home section article div{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    height:30%;
    border-radius:0 0  8px 8px ;
    padding:  3px 20px; 
}
.home section article div h3{
    font-size: 25px;
    letter-spacing: .25rem;
    color: var(--head-color);
}
.home section article div h6{
    font-size: 18px;
    letter-spacing: .28rem;
    color: var(--head-color);
}
.home section article div span{
    display:block;
    font-size: 14px;
    letter-spacing: .1rem;
    color: #999;
}

.home section article div button {
    width: 100px;
    height: 30px;
    background-color: #476A2E;
    border: none;
    border-radius: 7px;
    transition: 0.3s linear;
    margin: 5px 0;
}

.home section article div .Detail {
    text-decoration: none;
    color: #FFFF;
    letter-spacing: 0.25rem;
    text-transform: capitalize;
    transition: 0.3s linear;
}

.home section article div .Detail:hover {
    color: var(--font-color);
}

.Error button:hover {
    background-color: #D4E6A5;
    border: 1px dashed #476A2E;
}

    

@media (max-width:1024px) {
.home section {
display: grid;
    grid-template-columns: repeat(2, 400px);
    gap: 30px;
    margin-top: 20px;


}
}
@media (max-width:768px) {
.home section {
display: grid;
    grid-template-columns: repeat(1, 400px);
    gap: 30px;
    margin-top: 20px;


}
}