:root{
    --back-color:#F1F5F8;
    --font-color: #4A4D22;
    --head-color:#222222;
}

.about{
    width:100%;
    height:100%;
    background-color: var(--back-color);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.about h2{
    font-size: 30px;
    font-weight: 700;
    letter-spacing: .25rem;
    color:var(--head-color);
    margin-bottom:20px;
}
.about p {
    width: 600px;
    text-align: left;
    font-size: 16px;
    font-weight: 400;
    line-height:1.8;
    letter-spacing:0.15rem;
    font-weight: 300;
    color: var(--font-color);

}


@media (max-width:768px) {
.about p {width: 400px;}
}
    
