:root {
    --back-color: #F1F5F8;
    --font-color: #4A4D22;
    --head-color: #222222;
}


.main{
width:100%;
height:200px;
display:flex;
align-items: center;
justify-content:center;
}
.main .circle{
    width:100px;
    height:100px;
    border:5px solid ;
    border-color: var(--font-color) transparent var(--font-color) transparent;
    border-radius: 50%;
    transition: .5s linear;
    animation: loding 3s linear infinite alternate
}
@keyframes loding {
    0%{
        width: 50px;
        height: 50px;
        transform: rotate(180deg);
       

    }
    
}
