:root {
    --back-color: #F1F5F8;
    --font-color: #4A4D22;
    --head-color: #222222;
}

.Error {
    width: 100%;
    background-color: var(--back-color);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.Error p{
    font-size: 25px;
    font-weight:500;
    letter-spacing: 0.25rem;
    color:var(--head-color);
    margin-bottom:20px;
}

.Error button{
    width:140px;
    height: 40px;
    background-color:#476A2E;
    border:none;
    border-radius: 7px;
    transition: 0.3s linear;
}
.Error button .back{
    text-decoration: none;
    color:#FFFF;
    letter-spacing: 0.25rem;
    text-transform: capitalize;
    transition: 0.3s linear;
}
.Error button .back:hover{color:var(--font-color);}
.Error button:hover{
  background-color:#D4E6A5;
    border :1px dashed #476A2E;
}